/* General Items Content */
.items-content {
  font-family: Arial, sans-serif;
  background-color: #eeeef0;
  padding: 0px 20px;
}

/* Header Section */
.items-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.items-header h1 {
  color: #333333;
  font-family: Nunito;
  font-weight: 800;
}

.items-actions button {
  font-family: Nunito;
  font-weight: 600;
  margin-left: 10px;
  padding: 10px 15px;
  border: none;
  border-radius: 7px;
  cursor: pointer;
}

.items-add-button {
  background-color: #456efe;
  color: white;
}

.items-add-button:hover {
  background-color: #3d60de;
}

.items-category-button {
  background-color: white;
  color: rgb(32, 32, 32);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}

.items-category-button i {
  margin-right: 3px;
}

.items-category-button:hover {
  background-color: rgb(241, 241, 241);
}



.items-brand-button {
  background-color: white;
  color: rgb(32, 32, 32);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}

.items-brand-button i {
  margin-right: 3px;
}

.items-brand-button:hover {
  background-color: rgb(241, 241, 241);
}

/* View switch Styling*/
.view-switch {
  text-align: left;
  margin-left: 20px;
  gap: 20px;
}

/* Individual Buttons */
.view-switch button {
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  border: none;
  font-family: nunito;
  border-radius: 7px;
  color: rgb(118, 118, 118);
  background-color: white;
  cursor: pointer;
}

.view-switch button:first-child {
  border-radius: 10px 0 0 0; /* Round top-left and bottom-left */
}

.view-switch button:nth-child(2) {
  border-radius: 0;
}


.view-switch button:last-child {
  border-radius: 0 10px 0px 0; /* Round top-right and bottom-right */
}

/* Active Button Style */
.view-switch button.active {
  background-color: #456efe;
  color: white;
  border-color: #456efe;
}

/* Button Hover Effect */
.view-switch button:hover:not(.active) {
  background-color: #f0f0f0;
}


/* Items Table Content */
.items-table-content {
  font-family: Nunito;
  padding: 5px 20px 20px 20px;
  background-color: white;
  border-radius: 20px;
}

.items-filters {
  display: flex;
  align-items: center;
  margin-top: 20px;
  gap: 15px;
}

.items-search-filter {
  display: flex;
  align-items: center;
  gap: 10px;
}

.items-search-filter label {
  font-size: 14px;
  color: #555;
}

.items-search-filter input {
  padding: 8px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  width: 200px;
}

.items-category-filter{
  display: flex;
  gap: 5px;
  align-items: center;
  font-size: 14px;
  color: #555;
}

.items-category-filter select {
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  font-family: nunito;
  cursor: pointer;
}

.items-brand-filter{
  display: flex;
  gap: 5px;
  align-items: center;
  font-size: 14px;
  color: #555;
}

.items-brand-filter select {
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  font-family: nunito;
  cursor: pointer;
}

.items-entry-selector {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  color: #555;
}

.items-entry-selector select {
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}

.items-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.items-table th, .items-table td {
  color: #333333;
  padding: 10px;
  text-align: center;
  border-bottom: 1.5px solid #e4e4e4;
}

.items-actions-dropdown {
  position: relative;
}

.items-action-button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.items-dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  border: 1px solid #ddd;
  padding: 10px;
  right: 0;
  z-index: 1;
}

.items-actions-dropdown:hover .items-dropdown-content {
  display: block;
}

.items-pagination {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.items-pagination button {
  padding: 10px 10px;
  margin: 0 5px;
  background-color: #456efe;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.items-pagination button:hover {
  background-color: #3d60de;
}






